/*                                                      <STOP-SCROLL>                                                                                                                                                            */
/*                                                      <BURGER>                                                                                                                                                            */
* {
  padding: 0px;
  margin: 0px;
  border: 0px;
}

*, *::before, *::after {
  -moz-box-sizing: inherit;
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

:focus, :active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

aside, nav, footer, header, section {
  display: block;
}

html, body {
  position: relative;
  min-width: 320px;
}

html {
  box-sizing: border-box;
}

body {
  height: 100%;
  overflow-x: hidden;
  line-height: 1;
  font-size: 14px;
  font-family: Arial, Helvetica, sans-serif;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
  color: inherit;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  font-size: inherit;
  margin: 0;
}

ul, ol, li, p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

button:focus, input:focus {
  outline: transparent;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #F5F5F5;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.row {
  display: flex;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header__container {
  max-width: 1220px;
  margin-right: auto;
  margin-left: auto;
}

.header__wrapper {
  margin-top: 50px;
  display: flex;
  justify-content: space-between;
}

.header__logo {
  max-width: 200px;
}

.header__logo img {
  width: 100%;
}

.header__content {
  padding-top: 40px;
  max-width: 1012px;
  display: flex;
  height: 100px;
}

.content__card {
  color: #000000;
  margin-right: 50px;
}

.content__card .card__title {
  margin-bottom: 11px;
  padding-bottom: 10px;
  font-weight: 700;
  font-size: 19px;
  line-height: 1.21;
  text-transform: uppercase;
  max-width: 300px;
  border-bottom: 2px solid #DB0000;
}

.content__card .card__text {
  font-weight: 400;
  font-size: 12px;
  line-height: 1.25;
  text-transform: uppercase;
}

.content__social {
  display: flex;
  margin-right: 50px;
}

.content__social .social__wrapper {
  display: flex;
  align-items: center;
  margin-right: 30px;
}

.content__social .social__wrapper .social__img {
  width: 100%;
  margin-right: 10px;
}

.content__social .social__wrapper .social__img img {
  max-width: 100%;
}

.content__social .social__wrapper .social__img:hover {
  opacity: 0.4;
}

.content__file .file__wrapper {
  display: flex;
}

.content__file .file__img {
  max-width: 100%;
  margin-right: 21px;
}

.content__file .file__img img {
  width: 100%;
}

.content__file .file__img:hover {
  opacity: 0.4;
}

.content__file .file__text {
  max-width: 100px;
  text-decoration: none;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.2;
  color: #292F36;
}

.content__file .file__text:hover {
  text-decoration: underline;
}

.header__content--wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.header__nav {
  display: flex;
  background-color: #DBDBDB;
}

.header__nav .nav {
  width: 100%;
}

.header__nav .nav-ul {
  height: 40px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.header__nav .nav-ul li {
  text-align: center;
}

.header__nav .nav__link {
  padding-top: 5px;
  padding-bottom: 5px;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.22;
  text-transform: uppercase;
  color: #000000;
}

.header__nav .nav__link:hover {
  text-decoration: none;
  color: #BF0000;
  font-weight: 700;
}

.header__nav .nav__link:focus {
  text-decoration: none;
  color: #BF0000;
  font-weight: 700;
}
